.peliculas-buscar {
  display: grid;
  grid-template-columns: repeat(auto-fill, 300px);
  gap: 40px;
  padding: 30px;
  justify-content: center;
  .ant-card-meta-title {
    font-size: 15px;
    font-weight: bold;
  }

  .ant-card-cover {
    img {
      height: 450px;
      width: auto;
    }
  }
}
